import React from 'react'
import "../Css/Mediaplayer.css"
import Draggable from 'react-draggable';
import ReactPlayer from 'react-player'
import { Col } from 'react-bootstrap';
function Mediaplayer() {
    return (
<Col xs={6} lg={4}>
        <Draggable handle=".mydivheader">
            
                <Col xs={12} lg={12} className="mydiv">
                <div className="mydivheader">Click here to move</div>
                
                <ReactPlayer  className="player" width="100%" height  playing={true} controls url='https://youtu.be/OHgMppzCFpA' />
                
            </Col>

        </Draggable>
        </Col>
    )

}


export default Mediaplayer
